<template>
  <div id="app">
    
    <div id="fb-root"></div>



    <app-bar v-bind:user="user"></app-bar>
    <transition name="slide-fade">
      <router-view v-bind:user="user"></router-view>
    </transition>
  </div>
</template>

<script>
import firebase from 'firebase'
import fastclick from 'fastclick'
import AppBar from '@/components/AppBar'


var config = {
  apiKey: "AIzaSyA_2Bu81WuWrjLoJHP3EFDo41jYgNJ8BUM",
  authDomain: "validately-948d9.firebaseapp.com",
  databaseURL: "https://validately-948d9.firebaseio.com",
  projectId: "validately-948d9",
  storageBucket: "validately-948d9.appspot.com",
  messagingSenderId: "354991934469"
};
firebase.initializeApp(config);

export default {
  name: 'app',
  components: {
    AppBar
  },
  created: function(){
    var _this = this;
    firebase.auth().onAuthStateChanged(function(user) {
      if (user) {
          // console.log(user, "signed in!")
         _this.user = user;

         // console.log(_this.user.uid);

         var users = firebase.database()
           .ref('/users/')
           .child(_this.user.uid)
           .once('value', function(snapshot){
             var exists = (snapshot.val() !== null);
             // console.log(exists);

            if (!exists) {
              users = firebase.database().ref('/users/'+_this.user.uid).set({
                displayName: _this.user.displayName,
                email: _this.user.email
              })
            } else {
              console.log(_this.user.email, 'user already exists')
            }

           })

      } else {
        // console.log(_this.$route)
        console.log("not signed in")
      }
    });


    // FastClick
    if ('addEventListener' in document) {
        document.addEventListener('DOMContentLoaded', function() {
            FastClick.attach(document.body);
        }, false);
    }

    

    



    

  },
  data () {
    return {
      user: {}
    }
  }
}
</script>

<style lang="scss">

* {
    -webkit-touch-callout:none;                /* prevent callout to copy image, etc when tap to hold */
    -webkit-text-size-adjust:none;             /* prevent webkit from resizing text to fit */
    -webkit-tap-highlight-color:rgba(0,0,0,0); /* prevent tap highlight color / shadow */
    -webkit-user-select:'text';                  /* prevent copy paste, to allow, change 'none' to 'text' */
}


.content-container {
  padding-top: 60px;
  position: relative;
  z-index: 1;
}

#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
  background: white;
  padding: 0;
  margin: 0;
  
}

.app-bar {
  position: fixed;
  top: 0;
  z-index: 9999;
}

body {
  margin: 0;
  background: #F6F5F1;
}


h1 {
  margin: 0;
}

a {
  text-decoration: none;
  color: #333;
}


/* Enter and leave animations can use different */
/* durations and timing functions.              */
.slide-fade-enter-active {
  transition: all .3s ease;
  transition-delay: .35s;
}
.slide-fade-leave-active {
  transition: all .3s ease;
}


.slide-fade-enter, .slide-fade-leave-to
/* .slide-fade-leave-active for <2.1.8 */ {
  transform: translateX(10px);
  opacity: 0;
}

</style>



